body {
  box-sizing: border-box;
  --link-color: #00acdc;
  margin: 0;
  padding: 0;
  font-family: Roboto, serif;
  font-size: 12px;
  font-weight: 500;
}

.header {
  background-color: #fff;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  margin: 0 auto 0 0;
  display: flex;
}

.nav {
  flex-direction: row;
  align-items: center;
  height: 100%;
  display: flex;
}

.header__navigation {
  flex-direction: row;
  align-items: center;
  height: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.navigation__link {
  text-transform: uppercase;
  display: block;
  position: relative;
}

.navigation__link:not(:last-child) {
  margin-right: 20px;
}

.navigation__link:hover {
  color: var(--link-color);
}

.navigation__link > * {
  color: inherit;
  cursor: pointer;
  padding: 23px 0;
  text-decoration: none;
}

.is-active {
  color: var(--link-color);
}

.is-active:after {
  content: "";
  background-color: var(--link-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  top: 33.5px;
  left: 0;
}
